<template>
	<div :class="'app'">
	<div :class="navButtonClass" @click="toggleNav()">
		<span></span>
		<span></span>
		<span></span>
		<span></span>
	</div>
	<div :class="navigatorClass">
		<div :class="'navigator-content'">
			<div :class="'navigator-header'">
				<h3>ngx-smooth-dnd</h3>
				<div :class="'divider'"></div>
			</div>
			<div>
				<div v-for="section in pages" :key="section.title" :class="'menu-section'">
					<h4>{{section.title}}</h4>
					<ul>						
						<li v-for="page of section.pages" :key="page.title" :class="page.selected ? 'selected' : ''" @click="selectPage(page)">{{page.title}}</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
	<div :class="'content'">
		<div :class="headerClass">
			{{selectedPage.title}}
			<div :class="'source-code'" @click="openCode()">
				<img src="data:image/svg+xml;base64,"
				 alt="" />
				<span>source</span>
			</div>
		</div>
		<div :class="'demo'">
			<Simple v-if="selectedPage.page == 'simple'"></Simple>
			<SimpleScroller v-else-if="selectedPage.page == 'simple-scroller'"></SimpleScroller>
			<SimpleHorizontal v-else-if="selectedPage.page == 'simple-horizontal'"></SimpleHorizontal>
			<!-- <app-groups v-if="selectedPage.page == 'groups'"></app-groups>
			<app-copy v-if="selectedPage.page == 'copy'"></app-copy>
			<app-nested v-if="selectedPage.page == 'nested'"></app-nested>
			<app-lock-axis v-if="selectedPage.page == 'lock-axis'"></app-lock-axis>
			<app-drag-handle v-if="selectedPage.page == 'drag-handle'"></app-drag-handle>
			<app-drag-class v-if="selectedPage.page == 'drag-class'"></app-drag-class>
			<app-drag-delay v-if="selectedPage.page == 'drag-delay'"></app-drag-delay>
			<app-transition-duration v-if="selectedPage.page == 'transition-duration'"></app-transition-duration>
			<app-cards v-if="selectedPage.page == 'cards'"></app-cards>
			<app-form v-if="selectedPage.page == 'form'"></app-form> -->
		</div>
	</div>
</div>
</template>

<script>
import Pages from "./pages";
import { Simple, SimpleHorizontal, SimpleScroller } from './pages';
export default {
	name: "Demo",
	components: {
		Simple, SimpleHorizontal, SimpleScroller
	},
  data: function() {
    return {
      isNavOpen: true,
			selectedPage: Pages[0].pages[0],
			pages: Pages
    };
  },
  methods: {
    toggleNav: function() {
      this.isNavOpen = !this.isNavOpen;
    },
    selectPage: function(page) {
      this.selectedPage = page;
    }
  },
  computed: {
    navButtonClass: function() {
      return `nav-button${this.isNavOpen ? " open" : " closed"}`;
    },
    navigatorClass: function() {
      return `navigator${this.isNavOpen ? " open" : " closed"}`;
    },
    headerClass: function() {
      return `header${this.isNavOpen ? " open" : " closed"}`;
    }
  }
};
</script>

<style src="../style.css"/>




